[role=main]{
  padding-top: 78px !important;
}

.cp{
  cursor: pointer;
}

.navbar-header-text{
  margin: 0;
  color: #ffffff;
  text-align: center;
  font-size: 22px;
  padding-top: .95rem;
  padding-bottom: .85rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, .25);
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .25);
}

.country{
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  padding: 17px 28px;
  background-color: #2092ed;
  margin: 10px 0;
  color: #fff;
  cursor: pointer;
  transition: box-shadow 1s;
}

.country:hover{
  box-shadow: 0 13px 11px 0 rgba(0,0,0,0.2);
}

.highly-affected{
  margin: 15px 0;
}

.country-list{
  padding-left: 15px;
  padding-right: 15px;
}

.top-country-list{
  text-align: center;
  line-height: 40px;
}

.cases{
  font-size: 15px;
  padding: 10px 0;
  font-size: x-large;
  font-family: inherit;
}

.country-title {
  border-bottom: 2px solid blue;
  padding: 2px 0;
}

.card-data{
  color: #ffff;
  background: #000000;
  box-shadow: 1px 10px 10px 0px rgba(0,0,0,0.75);
  -webkit-box-shadow: 1px 10px 10px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 1px 10px 10px 0px rgba(0,0,0,0.75);
  margin: 7px 0;
  cursor: pointer;
}

.confirmed{
  background: #ffc107;
}

.death{
  background: #dc3545;
}

.recover{
  background: #28a745;
}

.input-search-box{
  margin: 10px 0;
  padding: 0px 15px;
}

.country-name{
  font-weight: 700;
  font-size: 18px;
}

.total-confirmed{
  font-weight: 900;
  color: lightblue;
}

.navbar-toggler{
  position: absolute;
  top: 0;
  left: 0;
  outline: none;
  z-index: 1;
  height: 100%;
  border: 0
}

.navbar-toggler:focus{
  border: 0;
  outline: none;
}

.full-width-sidebar{
  width: 100%;  
  
}

.link{
  color: #2092ed !important;
}

.country-info{
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 12px;
  font-family: unset;
  font-weight: 900;
}

.single-country-death{
  background-color: rgba(255,79,79,.2);
}

.single-country-death h5{
   color: #ff4f4f;
}


.single-country-recovered{
  background-color: rgba(0,189,133,.2);
}
.single-country-recovered h5{
  color: #00bd85;
}

.single-country-confirmed{
    background-color: rgba(37,127,249,.2);
}

.highly-affected-data{
  -webkit-transition: all .2s ease-in;
  -moz-transition: all .2s ease-in;
  -ms-transition: all .2s ease-in;
  -o-transition: all .2s ease-in;
  transition: all .2s ease-in;
}

.highly-affected-data:hover{
  -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
     transform: scale(1.1);
}

.single-country-confirmed h5{
  color: #257ff9;;
}

.single-country-name{
  text-align: center;
  margin: 20px;
  font-weight: 700;
  font-size: 32px;
}

/* Responsive Designing */

@media (max-width: 768px) {
  .show-sidebar{
    display: none;
  }

}

@media (min-width: 769px){
  .navbar-toggler{
    display: none;
  }
}